@import url(https://fonts.googleapis.com/css2?family=Anton&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+KR&display=swap);
html,body{margin:0;padding:0;width:100%;height:100%;overflow:hidden;font-family:"Noto Sans KR",sans-serif;font-size:calc(15px + (100vw - 1500px)/400);background-image:url(/discharge-dday/static/media/k9-thunder.12340e43.jpg);background-repeat:no-repeat;background-size:cover;background-position-y:80%}#root{height:100%}
#now-time{background-image:url(/discharge-dday/static/media/camouflage.e8bc64bd.png);background-position:38% 46%;display:flex;flex-direction:column}#now-title{margin:0 0;font-size:2rem;line-height:2rem}#now-timer{margin:.5rem auto 0;font-size:4rem;line-height:4rem}
#time-left{background-image:url(/discharge-dday/static/media/camouflage.e8bc64bd.png);background-position:67% 98%;display:flex;flex-direction:column;align-items:flex-start}.left-title{display:inline;margin:0;font-size:3rem;line-height:3rem}.left-text{display:inline;margin:.5rem 0 0;text-align:justify;line-height:4.5rem;font-size:4rem}@media(min-width: 335px)and (max-width: 757px){.left-text{font-size:3.5rem}.left-text#left-day{display:block;text-align:left}.left-text#left-time{display:block;text-align:left}}.progress-group{margin-top:1rem;width:100%;display:flex;justify-content:center;align-items:center}.left-progress{display:inline;width:100%;margin-right:.5rem;-webkit-appearance:none;appearance:none}.left-progress::-webkit-progress-bar{background:#f0f0f0;border-radius:10px}.left-progress::-webkit-progress-value{border-radius:4px;background:#55614f;background:linear-gradient(to right, #6f6e2b, #6B8E23)}.left-percent{display:inline;font-size:1rem;line-height:1rem}.left-info{background-image:url(/discharge-dday/static/media/camouflage.e8bc64bd.png);background-position:14% 36%;display:flex;flex-direction:column}
.main-component{height:100%;margin:0 1rem 0;display:flex;align-items:center}.container{margin:0 auto;max-width:66rem}.section{display:block;max-width:46rem;margin:0 auto 1rem;padding:1rem;border-radius:15px;color:#fff;text-align:center}
